.ypadbox-1 {
  padding: 1rem 0; }

.ypadbox-2 {
  padding: 2rem 0; }

.padbox-1 {
  padding: 1rem; }

.padbox-2 {
  padding: 2rem; }

.head-flex {
  display: flex;
  align-items: center;
  margin: 0.3rem 0 1.4rem 0;
  .border-title {
    margin: 0; } }

.relative {
  position: relative; }

.centered-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%; }

.centered-box {
  width: 100%;
  max-width: 400px; }

.card {
  background-color: white;
  box-shadow: 0 1px 1px rgba(black, 0.15);
  border-radius: 2px; }

.card-list-item {
  @extend .card;
  @extend .padbox-1;
  margin-bottom: 1rem; }

.flexbox {
  display: flex;
  align-items: center; }

.fullh {
  height: 100%; }

.scrollable {
  max-height: 100%;
  overflow-x: hidden;
  overflow-y: auto; }

@for $i from 1 through 12 {
  .flex-#{$i} {
    flex: $i; } }

.sright {
  margin-right: 1rem; }

.sdown {
  margin-bottom: 1rem; }

.txt-center {
  text-align: center; }

.txt-smaller {
  font-size: 0.8em; }

.vam {
  vertical-align: middle; }

%button-reset {
  appearance: none;
  border: none;
  background-color: transparent;
  padding: 0;
  margin: 0;
  &:focus {
    outline: none; } }

.logo-m {
  width: 200px; }

.txt-link {
  color: $primary-color;
  text-decoration: none;
  font-weight: bold;
  &:focus {
    outline: none; }
  &:focus, &:hover {
    text-decoration: underline; } }

.txt-light {
  color: rgba(black, $p3); }

.mini-row {
  margin-bottom: 0.3rem; }

.clearfix {
  @include clearfix; }

.marginless {
  margin: 0 !important; }

.y-separator {
  padding: 0;
  border: 0;
  border-top: 1px solid rgba(black, $p5);
  margin: 0.5rem auto 1rem auto; }

.companion-icon {
  margin-right: 0.5em;
  font-size: 1.2em; }

// Percentages columns

.float-col {
  float: left; }

.col-25 {
  width: 25%; }

.col-50 {
  width: 50%; }

.col-75 {
  width: 75%; }

.col-30 {
  width: 30%; }

.col-70 {
  width: 70%; }

// Legacy centering
.centered-outer {
  display: table;
  position: absolute;
  height: 100%;
  width: 100%; }

.centered-middle {
  display: table-cell;
  vertical-align: middle;
  text-align: center; }

.centered-inner {
  margin: 0 auto;
  display: inline-block; }

// Empty message
.empty-message {
  user-select: none;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 450px;
  text-align: center;
  .icon {
    margin-bottom: 1rem;
    i {
      color: $primary-color;
      font-size: 4rem; } }
  .legend {
    color: rgba(black, $p4); } }







